@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Rubik:wght@400;500;700&display=swap");
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  background-color: var(--color-white);
  font-weight: 300;
  font-size: 17px;
  line-height: 26px;
  color: #443737;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 768px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.btn-reset {
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.small-title {
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  color: #7fb800;
}

.big-title {
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 76px;
  color: #1d4d4f;
}

.btn {
  display: inline-block;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #ffffff;
  background: #7fb800;
  border-radius: 29px;
  padding: 16px 38px;
  transition: box-shadow 0.3s, background-color 0.3s;
}

.btn:hover {
  background-color: #3a9118;
  transition: box-shadow 0.3s, background-color 0.3s;
  box-shadow: linear-gradient(334.24deg, #7fb800 -9.84%, #0f9b0f 121.28%);
}

.date {
  font-family: Lato;
  font-style: italic;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  color: #7fb800;
}

.author {
  font-family: Lato;
  font-style: italic;
  font-weight: 400;
  font-size: 17px;
  line-height: 26px;
  color: #888888;
}
