@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin lg {
  @media (max-width: (1600px)) {
    @content;
  }
}

@mixin lg-min {
  @media (max-width: (1400px)) {
    @content;
  }
}

@mixin md {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin md-min {
  @media (max-width: (1000px)) {
    @content;
  }
}

@mixin sm {
  @media (max-width: (860px)) {
    @content;
  }
}

@mixin sm-min {
  @media (max-width: (600px)) {
    @content;
  }
}

@mixin xs {
  @media (max-width: (420px)) {
    @content;
  }
}
